.page {
  scroll-behavior: smooth;
  background: #fff;
  font-family: Poppins, sans-serif;
  font-size: 23px;
  line-height: 140%;
}

.page__body {
  margin: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.page__menu:target:target {
  opacity: 1;
  pointer-events: all;
}

.header {
  box-sizing: border-box;
  background-image: url("Cover_photo.aae76935.png");
  background-position: 85% 15%;
  background-size: cover;
  height: 100vh;
}

.header__title {
  letter-spacing: -1.5px;
  text-align: center;
  vertical-align: middle;
  color: #fff;
  height: 36px;
  margin: 0;
  padding: 356px 20px 88px;
  font-family: Poppins, sans-serif;
  font-size: 36px;
  font-weight: 700;
  line-height: 100%;
  top: 356px;
  left: 21px;
}

.header__bottom {
  padding: 0 20px 40px;
}

.top-bar {
  justify-content: space-between;
  align-items: flex-start;
  padding: 24px 20px 88px;
  display: flex;
}

.top-bar__icon {
  gap: 24px;
  display: flex;
}

.top-bar__logo {
  display: flex;
}

.menu {
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #c2c2c2;
  height: 100vh;
  font-weight: 600;
}

.menu-top {
  margin-bottom: 24px;
}

.menu__nav {
  padding-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__phone-number {
  color: inherit;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
  text-decoration: none;
  display: block;
}

.menu__book-a-test-ride {
  width: fit-content;
  color: inherit;
  letter-spacing: 2px;
  border-bottom: 1px solid #1d1d1d;
  padding-bottom: 7px;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.nav__link {
  color: inherit;
  letter-spacing: 2px;
  font-size: 22px;
  line-height: 22px;
  text-decoration: none;
}

.nav__list {
  flex-direction: column;
  gap: 32px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
}

.icon--phone {
  background-image: url("Icon-Phone-call.1491e815.png");
}

.icon--menu {
  background-image: url("Icon-Burger-menu.5d109225.png");
}

.icon--close {
  background-image: url("Buttons.9e55e38a.png");
}
